/* ====================== */
/* VARIABLES */
/* ====================== */

html {
  font-size: 16px;
}

#intro {
  display: block;
}

// colors
$squid-ink: #232f3e;
$bg-color: white; // rgba(255, 97, 56, 0.8);
$bg-color2: white; // rgb(246, 233, 218); //rgb(154, 222, 222);
$bg-color-dark: rgba(252, 244, 232, 0.98);
$color: #ff8f00;
$test-color: $color;

// Fonts
$font-color: $squid-ink;
$main-font: AmazonEmber;
$font-heavy: AmazonEmberDisplayHeavy;
$font-light: AmazonEmberDisplayLight;
$font-mono: AmazonEmberMono;

// Line-Height
$base-line-height: 1.5;
$header-line-height: 1.25;
/* ====================== */
/* BASE STYLLES */
/* ====================== */
* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  min-height: 1280px;
  color: #3b3b3b;
  font-size: 24px;
}

p.intro-text {
  font-size: 1.1rem;
  text-align: left;
  color: $font-color;
  font-family: $main-font;
  color: white;
}

p,
h1,
h2,
h3,
h4,
a {
  margin: 0;
  font-weight: 400;
}

a:visited,
a:hover {
  // color: $color;
  text-decoration: none;
  // border-bottom: 2px solid currentColor;
}

a {
  color: $squid-ink;
  text-decoration: underline;
}

a:hover {
  color: $font-color;
  background-color: $color;
  text-decoration: none;
  // border-bottom: 2px solid currentColor;
}

#intro {
  max-width: 45rem;
  margin: 1rem auto;
  text-align: center;
}

.cardSpan {
  cursor: pointer;
}

#toc {
  padding-left: 4rem;
}

#intro__icon h2 {
  // padding-left: 2%;
  font-family: AmazonEmberDisplayHeavy;
  font-size: 1rem;
}

.text {
  h2 {
    font-size: 1.5rem;
    padding-left: 1px;
    font-family: AmazonEmberDisplayHeavy;
  }
}

.icon,
.text {
  vertical-align: middle;
  display: inline-block;
}
/* ====================== */
/* LAYOUT STYLES */
/* ====================== */

#ai {
  color: rgb(201, 190, 190);
  font-family: AmazonEmberDisplayHeavy;
}

.logo {
  color: white;
  width: 10rem;
}

#intro-icon {
  display: flex;
  // justify-content: center;
  // vertical-align: center;
  text-transform: uppercase;
  // letter-spacing: 1.5px;
  // padding: 2rem;
  font-weight: bold;
  color: white;
  flex-direction: row;
}

#intro-icon h2 {
  padding-left: 2%;
  font-family: AmazonEmberDisplayHeavy;
  font-size: 1.2rem;
  margin-top: 10px;
  margin-bottom: 10px;
}

body {
  background-color: $bg-color;
}

.section-title {
  font-family: AmazonEmberDisplayHeavy;
  font-size: 2.2rem;
  text-align: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: sans-serif;
}

header {
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.3);
  position: sticky;
  top: 0px;
  background: white;
  z-index: 999;
}

section {
  min-height: 100vh;
  max-height: 100vh;
  vertical-align: middle;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

section {
  margin-bottom: 24vh;
  h2 {
    padding-top: 20vh;
    font-size: 2.5rem;
    color: $font-color;
    font-family: $font-heavy;
  }
  h3 {
    font-size: 1.5rem;
    padding-top: 1rem;
    font-family: AmazonEmberDisplayHeavy;
    color: $font-color;
    opacity: 0.9;
  }
}

section p {
  text-align: center;
  justify-content: center;
  align-items: middle;
  vertical-align: middle;
  text-align: center;
  align-items: center;
  justify-content: center;
}

#intro-icon svg {
  color: black;
  fill: black;
}

#scrolly > * {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
}

#scrolly {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: block;
}

article {
  position: relative;
  padding: 0 1.5rem;
  // max-width: 60rem;
  vertical-align: middle;
  text-align: center;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  margin-top: 10px;
  margin: 0 auto;
  max-width: 50rem;

  p {
    font-size: 1.15rem;
    margin: 4px;
    padding: 1rem;
    padding-top: 2rem;
    justify-content: center;
    text-align: center;
    // border: 2px solid black;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.3);
    background-color: $bg-color;
  }

  h2 {
    background-color: $bg-color;
    display: inline-block;
    padding: 0px;
    margin: 0px;
  }

  h3 {
    display: none;
  }

  #end-p {
    display: none;
  }
}

figure {
  position: -webkit-sticky;
  position: sticky;
  height: 90vh;
  width: 100%;
  margin: 0;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  z-index: 0;
  vertical-align: center;
  top: 100px;
  justify-content: center;
  text-align: center;
}

#all {
  margin-bottom: 15vh;
}

section {
  // margin-bottom: 50vh;
  // margin-top: 10vh;

  h2 {
    padding-top: 0vh;
  }
}

// new
#introduction {
  min-height: 100vh;
  background-color: #232f3e;
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;

  h1 {
    color: white;
    font-family: $font-heavy;
    font-size: 5rem;
  }

  p {
    color: white;
    font-family: $font-heavy;
    opacity: 0.9;
    font-size: 1.45rem;
  }

  a,
  a:hover {
