    background-color: #232f3e;
    text-decoration: underline;
  }

  #subtitle {
    font-family: $font-light;
    font-size: 1.4rem;
    padding: 1rem;
    opacity: 0.8;
  }
}

#chart {
  outline: 2px solid black;
  height: 50vh;
  width: 50%;
  margin: 0 auto;
}

#train {
  margin-bottom: 25vh;
}
